<template>
  <div id="app">


  <fab :actions="fabActions"
       @cache="cache"
       @alertMe="alert"
  ></fab>


  </div>
</template>

<script>
export default {
  props: ['title'],
  data () {
    return {
      activeTab: ''
    }
  },
  created () {
    this.activeTab = this.$route.name
  },
  methods: {
    switchTab (tab) {
      this.$router.push({ name: tab })
    }
  },
  watch: {
    '$route.name' () {
      this.activeTab = this.$route.name
    }
  }
}
</script>
